import React from 'react';
import { useState } from "react";
import { Button, notification, Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';
import GridCardWrapper, {
  ImageWrapper,
  FavoriteIcon,
  ContentWrapper,
  LocationArea,
  TitleArea,
  PriceArea,
  RatingArea,
  MetaWrapper,
  ButtonGroup,
} from './AccountGridCard.style';
import AxiosFetch from 'library/hooks/AxiosFetch';
import stakeNft from 'web3/PremierStaking';
import unstakeNft from 'web3/PremierUnStaking';
import { approveAll } from 'web3/Premier';

const loadingIcon = <LoadingOutlined style={{ fontSize: 24, color: "white" }} spin />;

const GridCard = ({
  className,
  id,
  favorite,
  location,
  title,
  price,
  rating,
  editBtn,
  children,
  token_id,
  collectionFrom,
  collectionTo,
  vin,
  booking_id,
  booking,
  staking,
  account,
  updateTokenStaking,
  renting,
  isApproved
}) => {

  const [stakeStatus, setStatkeStatus] = useState(staking);
  const [bookingStatus, setBookingStatus] = useState(booking);
  const [isStakingLoading, setIsStakingLoading] = useState(false);

  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const handleStaking = async () => {    
    setIsStakingLoading(true);
    if(staking == 1)
    {
      await unstakeNft(token_id, account).then(
        (data) => {          
          if (data.success == false) {
            openNotificationWithIcon('error', 'Error!', `Error happened while unstaking, please try again later`)                            
          }
          else
          {
            updateTokenStakingStatus(token_id, 0);
            openNotificationWithIcon('success', 'Success', `You've removed your token from staking. You will not get award tokens from now.`)          
          }
          setIsStakingLoading(false);
        }
      );
    }
    else
    {
      await stakeNft(token_id, account).then(
        (data) => {          
          if (data.success == false) {
            openNotificationWithIcon('error', 'Error!', `Error happened while staking, please try again later`)                            
          }
          else
          {
            updateTokenStakingStatus(token_id, 1);
            openNotificationWithIcon('success', 'Success', `You are staking your token. You will get award tokens from now.`)          
          }
          setIsStakingLoading(false);
        }
      );
    }
  }

  const updateTokenStakingStatus = async(tokenId, _status) => {
    let url = '/updateTokenStakingStatus';
    let bodyStaking = {
      token_id: tokenId,
      status: _status
    }
    await AxiosFetch(url, bodyStaking, "POST");
    setStatkeStatus(_status)
  }

  const handleRenting = async () => {    
    if(stakeStatus == 0) {
      setIsStakingLoading(true);
      await stakeNft(token_id, account).then(
        (data) => {          
          if (data.success == false) {
            openNotificationWithIcon('error', 'Error!', `Error happened while staking, please try again later`)                            
          }
          else
          {
            updateTokenStakingStatus(token_id, 1);
            openNotificationWithIcon('success', 'Success', `You are staking your token. You will get award tokens from now.`)          
            renting(token_id, collectionFrom, collectionTo);
          }
          setIsStakingLoading(false);
        }
      );
    }
    else {
      renting(token_id, collectionFrom, collectionTo);
    }    
  }

  const handleApprove = async () => {
    await approveAll(account).then(
      (data) => {            
        if (data.success == false) {
          openNotificationWithIcon('error', 'Error!', `Error happened while approving, please try again later`)                            
        }
        else
        {
          openNotificationWithIcon('success', 'Success', `Your account has been approved for staking. Please click account button for staking or renting.`)                        
          window.location.reload();    
        }
      }
    );
  }

  const handleUnlockRequest = async () => {
    if(booking_id)
    {
      const body = {
        id: booking_id,
        status: "unlock_requested"
      }

      let url = '/updateBookingStatus';
      
      let res = await AxiosFetch(url, body, "POST");
      console.log(res)

      openNotificationWithIcon('success', 'Success', `You've requested unlock for this NFT. We will check and release NFT soon.`)          
      setBookingStatus('unlock_requested');
    }
    else
      openNotificationWithIcon('error', 'Error!', `Booking ID is undefined, please refresh the page again.`)                            
  }
  const buttonsByConditions = () => {
    if(bookingStatus) {      
      if(bookingStatus === 'booked') {      
        return (
          <Button className="stake_button approve_button" disabled={true}>
            Booked
          </Button>
        );
      }
      else if(bookingStatus === 'locked') {
        return (
          <Button className="stake_button approve_button" disabled={true}>
            Locked
          </Button>
        );
      }
      else if(bookingStatus === 'unlock_available') {
        return (
          <Button className="stake_button approve_button" onClick={handleUnlockRequest}>
            Request Unlock
          </Button>
        );
      }
      else if(bookingStatus === 'unlock_requested') {
        return (
          <Button className="stake_button approve_button" disabled={true}>
            Unlock Requested
          </Button>
        );
      }    
    }
    return (
      <ButtonGroup>
        <Button className="stake_button" onClick={handleStaking} disabled={isStakingLoading}>
          {
            isStakingLoading && <Spin indicator={loadingIcon} />
          }
          {
            (!isStakingLoading && stakeStatus == 1) && 'Unstake'
          }
          {
            (!isStakingLoading && stakeStatus == 0) && 'Stake'
          }
        </Button>
        <Button className="stake_button" onClick={handleRenting} disabled={isStakingLoading}>
          Use
        </Button>
      </ButtonGroup>
    );
  }
  return (
    <GridCardWrapper className={`grid_card`.trim()}>
      <ImageWrapper className="media_wrapper">{children}</ImageWrapper>
      <ContentWrapper className="content_wrapper">
        {location && <LocationArea>{location}</LocationArea>}
        {title && <TitleArea>{title}</TitleArea>}
        <MetaWrapper className="meta_wrapper">
          {price && <PriceArea className="price">{price}</PriceArea>}
          {rating && <RatingArea className="rating">{rating}</RatingArea>}          
          <RatingArea className="rating">1/{collectionTo - collectionFrom + 1} {title}</RatingArea>
        </MetaWrapper>
        {/* {
          isApproved && (
            buttonsByConditions()
          )
        }
        {
          !isApproved && (                          
            <Button className="stake_button approve_button" onClick={handleApprove} disabled={isStakingLoading}>
              Approve
            </Button>            
          )
        } */}
              
      </ContentWrapper>
    </GridCardWrapper>
  );
};

GridCard.propTypes = {
  className: PropTypes.string,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  price: PropTypes.string,
  rating: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  location: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  editBtn: PropTypes.element,
  viewDetailsBtn: PropTypes.element,
};

export default GridCard;
