import React, { useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Row, Col, Input, Select, Button, notification } from 'antd';
import FormControl from 'components/UI/FormControl/FormControl';
import DatePicker from 'components/UI/AntdDatePicker/AntdDatePicker';
import ImageUploader from 'components/UI/ImageUploader/ImageUploader';
import DatePickerRange from 'components/UI/DatePicker/ReactDates';
import Heading from 'components/UI/Heading/Heading';
import HtmlLabel from 'components/UI/HtmlLabel/HtmlLabel';
import { RentCarFormWrapper, FormTitle, PhotoUploadWrapper, FieldWrapper } from './RentCarForm.style';
import AxiosFetch from 'library/hooks/AxiosFetch';

const locationOptions = [
    { label: 'Orlando International Airport(MCO)', value: 'Orlando International Airport(MCO)' },
    { label: 'Other', value: 'other' },
];

export default function RentCarForm(props) {
    const { tokenId, collectionCount, account } = props;

    const [duration, setDuration] = useState({
        startDate: null,
        endDate: null,        
    });
    const [days, setDays] = useState(0);
    const [driverLicensePhotoArray, setDriverLicensePhotoArray] = useState([]);
    const [insurancePhotoArray, setInsurancePhotoArray] = useState([]);

    const {
        control,
        formState: { errors },
        handleSubmit,
    } = useForm();
    
    const updateSearchDataFunc = (value) => {
        setDuration({
          ...duration,
          startDate: value.setStartDate,
          endDate: value.setEndDate,
        });
        let startDate = new Date(value.setStartDate);
        let endDate = new Date(value.setEndDate);
        var difference = endDate.getTime() - startDate.getTime();
        var daysDifference = Math.ceil(difference / (1000 * 3600 * 24));

        var totalLockDays = daysDifference * collectionCount;
        console.log('daysDifference is ', daysDifference);
        setDays(totalLockDays)
    };

    const openNotificationWithIcon = (type, title, description) => {
        notification[type]({
          message: title,
          description: description,
          placement: 'bottomRight'
        });
    };

    const onSubmit = async (data) => {
        if(days > 730)
        {
            openNotificationWithIcon('error', 'Error!', 'Please input available rent days.');
            return;
        }        
        if(driverLicensePhotoArray.length == 0 || insurancePhotoArray.length == 0)
        {
            openNotificationWithIcon('error', 'Error!', 'Please upload Driver License and Insurance Photos.');
            return;
        }                

        let bookingData = data;
        bookingData.tokenId = tokenId;        
        bookingData.lockDays = days;
        bookingData.driverLicenses = driverLicensePhotoArray;
        bookingData.insurances = insurancePhotoArray;        
        bookingData.account = account;
        bookingData.startDate = duration.startDate;
        bookingData.endDate = duration.endDate;        
        
        console.log("booking data is ", bookingData);
        
        let url = '/createNewBooking';        
        let res = await AxiosFetch(url, bookingData, "POST");
        if(res.data.success){
            openNotificationWithIcon('success', 'Success!', 'Your booking has been successfully saved.');
        }
        else {
            openNotificationWithIcon('error', 'Error!', res.data.message);
        }
    };

    const onChangeDriverPhoto = (fileName) => {                
        setDriverLicensePhotoArray(oldArray => [...oldArray, fileName]);
    }

    const onChangeInsurancePhoto = (fileName) => {
        setInsurancePhotoArray(oldArray => [...oldArray, fileName]);
    }

    return (
        <RentCarFormWrapper>
            <FormTitle>Rent this Car with your Personal Data</FormTitle>
            <form className="form-container" onSubmit={handleSubmit(onSubmit)}>
                <Row gutter={30}>
                    <Col lg={12}>
                        <FormControl
                            label="First name"
                            htmlFor="firstName"
                            error={errors.firstName && <span>This field is required!</span>}
                        >
                        <Controller
                            name="firstName"
                            defaultValue=""
                            control={control}
                            rules={{ required: true }}
                            render={({ field: { onChange, onBlur, value } }) => (
                                <Input onChange={onChange} onBlur={onBlur} value={value} />
                            )}
                        />
                        </FormControl>
                    </Col>
                    <Col lg={12}>
                        <FormControl
                            label="Last name"
                            htmlFor="lastName"
                            error={errors.lastName && <span>This field is required!</span>}
                        >
                        <Controller
                            name="lastName"
                            defaultValue=""
                            control={control}
                            rules={{ required: true }}
                            render={({ field: { onChange, onBlur, value } }) => (
                                <Input onChange={onChange} onBlur={onBlur} value={value} />
                            )}
                        />
                        </FormControl>
                    </Col>
                </Row>
                <Row gutter={30}>
                    <Col lg={12}>
                        <FormControl
                            label="Date of birth"
                            htmlFor="dateOfBirthday"
                            error={
                                errors.dateOfBirthday && <span>This field is required!</span>
                            }
                        >
                            <Controller
                                name="dateOfBirthday"
                                defaultValue=""
                                control={control}
                                rules={{ required: true }}
                                render={({ field: { onChange, onBlur, value } }) => (
                                    <DatePicker
                                        onChange={onChange}
                                        onBlur={onBlur}
                                        value={value}
                                    />
                                )}
                            />
                        </FormControl>
                    </Col>
                    <Col lg={12}>
                        <FormControl
                            label="Phone number"
                            htmlFor="phoneNumber"
                            error={
                                errors.phoneNumber && (
                                    <>
                                        {errors.phoneNumber?.type === 'required' && (
                                            <span>This field is required!</span>
                                        )}
                                        {errors.phoneNumber?.type === 'pattern' && (
                                            <span>Please enter your valid number!</span>
                                        )}
                                    </>
                                )
                            }
                        >
                            <Controller
                                name="phoneNumber"
                                defaultValue=""
                                control={control}
                                rules={{
                                    required: true,
                                    pattern: /^[0-9]*$/,
                                }}
                                render={({ field: { onChange, onBlur, value } }) => (
                                    <Input onChange={onChange} onBlur={onBlur} value={value} />
                                )}
                            />
                        </FormControl>
                    </Col>
                </Row>
                <Row gutter={30}>
                    <Col lg={12}>
                        <FormControl
                            label="Email address"
                            htmlFor="email"
                            error={
                                errors.email && (
                                    <>
                                        {errors.email?.type === 'required' && (
                                            <span>This field is required!</span>
                                        )}
                                        {errors.email?.type === 'pattern' && (
                                            <span>Please enter a valid email address!</span>
                                        )}
                                    </>
                                )
                            }
                        >
                            <Controller
                                name="email"
                                defaultValue=""
                                control={control}
                                rules={{
                                    required: true,
                                    pattern: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/,
                                }}
                                render={({ field: { onChange, onBlur, value } }) => (
                                    <Input
                                        type="email"
                                        onChange={onChange}
                                        onBlur={onBlur}
                                        value={value}
                                    />
                                )}
                            />
                        </FormControl>
                    </Col>
                    <Col lg={12}>
                        <FieldWrapper>
                            <HtmlLabel htmlFor="dates" content="Dates" />
                            <DatePickerRange
                                startDateId="checkin-Id"
                                endDateId="checkout-id"
                                startDatePlaceholderText="Start Date"
                                endDatePlaceholderText="End Date"
                                updateSearchData={(value) => updateSearchDataFunc(value)}
                                numberOfMonths={1}
                                small
                            />
                            { (days > 0 && days < 730) && <span>NFT will be locked for { days } days.</span> }
                            { days > 730 && <span className="overflow_lockdays">One NFT can't be locked over 2 years. <br></br> Maximum Available Rent Days with this NFT: { Math.floor(730 / collectionCount) } days</span> }
                        </FieldWrapper>
                    </Col>
                    <Col lg={24}>
                        <FormControl
                            label="Location"
                            htmlFor="location"
                            error={errors.address && <span>This field is required!</span>}
                        >
                        <Controller
                            name="location"
                            defaultValue=""
                            control={control}
                            rules={{
                                required: true,
                            }}
                            render={({ field: { onChange, onBlur, value } }) => (
                                <Select
                                    options={locationOptions}
                                    onChange={onChange}
                                    onBlur={onBlur}
                                    value={value}
                                />
                            )}
                        />
                        </FormControl>
                    </Col>
                </Row>
                
                <PhotoUploadWrapper>
                    <div className="uploadItem">
                        <Heading content="Driver License Photo" as="h4" />
                        <ImageUploader onChange={onChangeDriverPhoto}/>
                    </div>
                    <div className="uploadItem">
                        <Heading content="Insurance Photo" as="h4"/>
                        <ImageUploader onChange={onChangeInsurancePhoto}/>
                    </div>
                </PhotoUploadWrapper>
                
                <div className="submit-container">
                    <Button htmlType="submit" type="primary">
                        Book
                    </Button>
                </div>
            </form>            
        </RentCarFormWrapper>
    );
}
