import React from 'react';
import { useEffect, useState } from "react";
import AccountSectionGrid from 'components/AccountSectionGrid/AccountSectionGrid';
import { PostPlaceholder } from 'components/UI/ContentLoader/ContentLoader';
import AxiosFetch from 'library/hooks/AxiosFetch';
import { Modal } from 'antd';
import RentCarForm from './RentCarForm';

const AgentItemLists = ({ account }) => {
  const [tokens, setTokens] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [tokenId, setTokenId] = useState(null);
  const [collectionCount, setCollectionCount] = useState(0);
  const [isApproved, setIsApproved] = useState(null);

  const fetchData = async () => {
    const url = '/getTokensByOwner';
    const body = {
      address: account,
    }
    const res = await AxiosFetch(url, body, "POST");
    if (res.data.success) {
      setIsApproved(res.data.isApproved);
      setTokens(res.data.data);
    }
  }

  const handleRenting = (tokenId, collectionFrom, collectionTo) => {
    setIsModalOpen(true);
    setTokenId(tokenId);
    setCollectionCount(collectionTo - collectionFrom + 1);
  }

  const handleModalClose = () => {
    setIsModalOpen(false);
  }
  useEffect(fetchData, [account]);  

  return (
    <div>
      {account === null &&
        <h3> You need to connect your wallet. </h3>
      }
      {account &&
        <AccountSectionGrid
          data={tokens}
          totalItem={tokens.length}
          columnWidth={[1 / 2, 1 / 2, 1 / 3, 1 / 4, 1 / 5, 1 / 6]}
          placeholder={<PostPlaceholder />}
          account={account}
          updateTokenStaking = {fetchData}
          renting = {handleRenting}
          isApproved = {isApproved}
        />
      }
      <Modal
          visible={isModalOpen}
          onCancel={() => handleModalClose()}
          footer={null}
          width="100%"
          maskStyle={{ backgroundColor: 'rgba(255, 255, 255, 0.9)' }}
          wrapClassName="review_modal"
        >
          <RentCarForm tokenId={tokenId} collectionCount={collectionCount} account={account}/>
        </Modal>
    </div>
  );
};

export default AgentItemLists;
