import styled from 'styled-components';
import { themeGet } from '@styled-system/theme-get';

export const RentCarFormWrapper = styled.div`
  @media (max-width: 767px) {
    padding-bottom: 50px;
  }
  h4 {
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    margin-bottom: 20px;
  }
  .image_uploader {
    display: block;
    overflow: hidden;
    margin-bottom: 30px;
  }
  .ant-upload.ant-upload-drag {
    border-radius: 3px;
    background-color: ${themeGet('color.2', '#F7F7F7')};
    border: 1px solid ${themeGet('border.0', '#EBEBEB')};
    &:not(.ant-upload-disabled):hover {
      border-color: ${themeGet('border.0', '#EBEBEB')};
    }
    .ant-upload.ant-upload-btn {
      padding: 56px 0;
      @media (max-width: 767px) {
        padding: 30px 0;
      }
      .ant-upload-drag-container {
        > div {
          flex-direction: column;
          justify-content: center;
          .ant-upload-text {
            padding: 0;
            margin: 5px 0 0;
            text-decoration: underline;
            background-color: transparent;
            color: ${themeGet('primary.0', '#008489')};
          }
        }
      }
    }
  }
  .ant-upload-list {
    &.ant-upload-list-picture-card {
      margin-top: 20px;
      .ant-upload-list-item {
        padding: 5px;
        border-radius: 3px;
        border-color: ${themeGet('border.3', '#E6E6E6')};
      }
    }
  }

  .submit-container {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 30px;
    @media (max-width: 767px) {
      width: 100%;
      padding: 22px 30px;
      flex-direction: row;
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 1;
      background-color: ${themeGet('color.1', '#ffffff')};
      border-top: 1px solid ${themeGet('border.3', '#E6E6E6')};
    }
    > button[type='submit'] {
      border-radius: 3px;
      height: 47px;
      border-color: ${themeGet('primary.0', '#008489')};
      background-color: ${themeGet('primary.0', '#008489')};
      font-size: 15px;
      font-weight: 700;
      text-transform: capitalize;
      padding-left: 28px;
      padding-right: 28px;
      line-height: 1;
      &::after {
        content: none;
      }
    }
  }
  .overflow_lockdays {
    color: red;
  }
`;

export const FormTitle = styled.h2`
  color: ${themeGet('text.0', '#2C2C2C')};
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  margin-bottom: 27px;
  @media (min-width: 1441px) {
    margin-bottom: 40px;
  }
  @media (max-width: 1200px) {
    font-size: 22px;
  }
`;

export const PhotoUploadWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  .uploadItem {
      width: 45%;      
  }
  @media (max-width: 500px) {
    display: block;
    .uploadItem {
        width: 100%;      
    }
  }
`;

export const FieldWrapper = styled.div`
margin-bottom: 30px;
@media (max-width: 375px) {
  margin-bottom: 25px;
}

label {
  font-size: 15px;
  color: ${themeGet('text.0', '#2C2C2C')};
  text-transform: capitalize;
  margin-bottom: 7px;
  display: block;
}

/* date picker component style */
.date_picker {
  margin-bottom: 0;
  > label {
    margin-bottom: 0;
    width: 100%;
  }
  .DateRangePicker {
    width: 100%;
    input,
    .DateRangePickerInput {
      border: 0;
      width: 100%;
      height: 54px;
      display: flex;
      padding: 0 15px;
      font-size: 15px;
      font-weight: 400;
      border-radius: 3px;
      align-items: center;
      justify-content: space-between;
      color: ${themeGet('text.0', '#2C2C2C')};
      background-color: ${themeGet('color.2', '#F7F7F7')};
      &::placeholder {
        font-size: 15px;
        color: ${themeGet('text.0', '#2C2C2C')};
      }
      .DateInput {
        width: auto;
        .DateInput_fang {
          top: 46px !important;
        }
      }
      .DateRangePickerInput_arrow {
        & + .DateInput {
          input {
            text-align: right;
          }
        }
      }
    }
    .DateRangePicker_picker {
      top: 56px !important;
      .DayPicker {
        width: 316px !important;
        .DayPicker_focusRegion {
          .DayPicker_transitionContainer {
            width: 316px !important;
            .CalendarMonth_caption {
              font-size: 16px;
              color: ${themeGet('text.0', '#2C2C2C')};
            }
          }
        }
      }
    }
  }
}

/* Room guest component style */
.view_with__popup {
  .popup_handler {
    button,
    button.ant-btn {
      border: 0;
      width: 100%;
      display: flex;
      padding: 0 25px;
      font-size: 15px;
      font-weight: 400;
      min-height: 54px;
      border-radius: 3px;
      align-items: center;
      justify-content: space-between;
      color: ${themeGet('text.0', '#2C2C2C')};
      background-color: ${themeGet('color.2', '#F7F7F7')};
      &::after {
        display: none;
      }
    }
  }
  &.active {
    .popup_container {
      max-width: 316px;
      padding: 25px 22px;
      position: absolute;
      left: 0;
      top: 56px;
      z-index: 2;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(0, 0, 0, 0.07);
      &::before {
        content: '';
        width: 14px;
        height: 14px;
        top: -8px;
        left: 36px;
        position: absolute;
        border-radius: 2px;
        transform: rotate(45deg);
        background-color: ${themeGet('color.1', '#ffffff')};
        border-top: 1px solid ${themeGet('border.3', '#E6E6E6')};
        border-left: 1px solid ${themeGet('border.3', '#E6E6E6')};
      }
    }
  }
}
`;

export default RentCarFormWrapper;